const flatten = (arr) => arr.flatMap(item => Array.isArray(item) ? flatten(item) : item);

/**
 * Transforms an element into a different representation.
 * @param {any} element - The element to be transformed.
 * @returns {Promise<any>|Text|any} - The transformed element.
 */
async function transformElement(element) {
  // If the element is falsy, return undefined
  if (!element) return;

  // If the element is a Promise, wait for it to resolve and return the result
  if (element instanceof Promise) {
    return await element;
  }

  // If the element is a string or number, create a Text node with its string representation
  if (typeof element === 'string' || typeof element === 'number') {
    return document.createTextNode(element.toString());
  }

  // If the element is a function, invoke it and return the result
  if (typeof element === 'function') {
    return element();
  }

  // Otherwise, return the element as is
  return element;
}

/**
 * Appends children to an element.
 * 
 * @param {HTMLElement} element - The element to append children to.
 * @param {Array|HTMLElement} children - The children to append.
 * @returns {HTMLElement} - The element with the appended children.
 */
const appendChildren = async (element, children) => {
  // Ensure children is an array
  const flattenChildren = Array.isArray(children)
    ? flatten(children)
    : [children];

  // Iterate over the children
  for (const child of flattenChildren) {
    // Only append if the child exists
    if (child) {
      // Transform the child element
      const transformChild = await transformElement(child);
      // Append the transformed child to the element
      element.appendChild(transformChild);
    }
  }

  return element;
};

// This function generates a JSX element based on the given tag and props.
// It supports both HTML tag names and functional components as tags.
const jsx = (tag, { ref, children, ...props } = {}) => {
  // If the tag is a string, create a new HTML element.
  if (typeof tag === 'string') {
    let element = document.createElement(tag);

    // Iterate over the props and set attributes or properties on the element.
    for (const [key, value] of Object.entries(props)) {
      if (value) {
        // If the prop value is a function, assign it as a property on the element.
        // Otherwise, set the attribute with the prop value.
        typeof value === 'function' ? element[key] = value : element.setAttribute(key, value);
      }
    }

    // If children are provided, append them to the element.
    if (children) {
      element = appendChildren(element, children);
    }

    // If a ref is provided, either assign it as a property on the element
    // or set the 'ref' attribute with the ref value.
    if (ref) {
      typeof ref === 'function' ? ref(element) : element.setAttribute('ref', ref);
    }

    // Return the generated element.
    return element;
  }

  // If the tag is a function, invoke it with the given props and return the result.
  if (typeof tag === 'function') {
    return tag({ ref, children, ...props });
  }

  // Throw an error if the tag type is invalid.
  throw new Error(`Invalid tag type: ${tag}`);
};

const Fragment = ({ children }) => appendChildren(document.createDocumentFragment(), children);

/**
 * Render the transformed element to the specified selector or to the document body.
 * @param {Element} element - The element to render.
 * @param {string} [selector] - Optional selector to specify the parent element.
 */
const render = async (element, selector) => {
    // If element is falsy, return early
    if (!element) return;

    // Transform the element asynchronously
    const transformedElement = await transformElement(element);

    // Specify the parent element as the document body if no selector is provided
    const parentElement = selector ? selector : document.body;

    // Append the transformed element to the parent element
    parentElement.appendChild(transformedElement);
};

const jsxs = jsx;

export { jsx, jsxs, Fragment, render};